import app from '@/common/app.js'; //公共变量
import request from '@/common/request.js';
import expand from '@/common/expand.js';
import {
    mapState
} from 'vuex';
export default {
    data() {
        return {
            serverPath: app.imagePath,
            num: 0,
            school_name: "",
            tabList: ['当月', '今年'],
            ranking: [], //前三名排名列表
            order: [] // 下面的顺序列表
        }
    },
    computed: {
        ...mapState(['configParams'])
    },
    onShow() {
        this.school_name = this.configParams.school_name;
        this.getYearDate();
    },
    onShareAppMessage(res) {
        expand.saveShare(app.share_code.SHARERANK_SHARE);
        return {
            title: this.school_name + '分享排名',
            path: '/pages_mine/myCard/shareRank'
        }
    },
    methods: {
        getNum(index) {
            this.num = index;
            console.log(this.num)
            if (index == 1) {
                this.getYearDate();
            } else {
                this.getMonthDate();
            }
        },
        getYearDate() { //获取当年的数据
            let date = new Date();
            let year = date.getFullYear();
            //获取排名
            request.http({
                url: '/app/ranking/getChaShare',
                method: "POST",
                data: {
                    year: year
                },
                header: 'application/x-www-form-urlencoded',
                success: (res) => {
                    if (res.data.code == app.RESPONSE_STATUS.REQUEST_SUCCESS) {
                        this.ranking = res.data.data.ranking;
                        this.order = res.data.data.order;
                    }
                },
                error: (res) => {

                }
            });
        },
        getMonthDate() { // 获取当月的数据
            let date = new Date();
            let year = date.getFullYear();
            let month = date.getMonth() + 1;
            //获取排名
            request.http({
                url: '/app/ranking/getChaShare',
                method: "POST",
                data: {
                    year: year,
                    month: month
                },
                header: 'application/x-www-form-urlencoded',
                success: (res) => {
                    if (res.data.code == app.RESPONSE_STATUS.REQUEST_SUCCESS) {
                        this.ranking = res.data.data.ranking;
                        this.order = res.data.data.order;
                    }
                },
                error: (res) => {

                }
            });
        }
    }
}
